.page__body {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  line-height: 160%;
  font-weight: 400;
  color: #444444;
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .page__body {
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .page__body {
    width: 1150px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: local("Open Sans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  display: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.button {
  background: #ffffff;
  border: 2px solid #444444;
  color: #000000;
  padding: 16px 70px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  background-color: #444444;
  color: #7c7c7c;
}

.main-header {
  display: flex;
  justify-content: center;
  position: relative;
  background-color: transparent;
  padding-top: 22px;
  padding-bottom: 22px;
  box-sizing: border-box;
  border-bottom: 1px solid #f1f1f1;
}
@media (min-width: 768px) {
  .main-header {
    padding: 0 50px;
    border: none;
  }
  .main-header::after {
    content: "";
    position: absolute;
    top: calc(100% - 65px);
    height: 65px;
    width: 100%;
    z-index: -1;
    background-color: #f9f9f9;
    border-top: 1px solid #f1f1f1;
  }
}
@media (min-width: 768px) {
  .main-header--inner::after {
    background-color: #f1f1f1;
  }
}

@media (min-width: 768px) {
  .main-header--inner .submenu {
    background-color: #f1f1f1;
  }
}

@media (min-width: 768px) {
  .main-nav {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 347px;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 1fr 250px 1fr;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 1fr 150px 1fr;
  }
}
.main-nav--closed .main-nav__toggle {
  width: 21px;
  height: 21px;
  position: absolute;
  top: 37%;
  right: 10%;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='14' viewBox='0 0 20 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20 0H0V2H20V0Z' fill='%23231F20'/%3E%3Cpath d='M20 6H0V8H20V6Z' fill='%23231F20'/%3E%3Cpath d='M20 12H0V14H20V12Z' fill='%23231F20'/%3E%3C/svg%3E%0A");
  background-size: 20px 14px;
  background-repeat: no-repeat;
  border-style: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__list-wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__list-wrapper {
    position: static;
    display: contents;
  }
}
.main-nav--opened .main-nav__toggle {
  width: 21px;
  height: 21px;
  position: absolute;
  top: 37%;
  right: 10%;
  background-image: url("data:image/svg+xml,%3Csvg width='21' height='20' viewBox='0 0 21 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21 1.36585L19.5659 0L10.5512 8.58537L1.43415 0L0 1.36585L9.01463 10.0488L0 18.6341L1.43415 20L10.5512 11.4146L19.5659 20L21 18.6341L11.9854 10.0488L21 1.36585Z' fill='black'/%3E%3C/svg%3E%0A");
  background-size: 21px 20px;
  background-repeat: no-repeat;
  border-style: none;
  background-color: transparent;
}
.main-nav--opened .main-nav__list-wrapper {
  display: block;
  position: absolute;
  width: 100%;
  background-color: #ffffff;
  top: calc(100% + 1px);
  left: 0;
  right: 0;
  z-index: 1;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list-wrapper {
  display: block;
  position: static;
  width: 100%;
  background-color: #ffffff;
  top: calc(100% + 1px);
  left: 0;
  right: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav--nojs .main-nav__list-wrapper {
    position: static;
    display: contents;
  }
}
.main-nav--nojs .main-nav__logo {
  text-align: center;
}

@media (min-width: 768px) {
  .main-nav__site-list {
    grid-column: 1/2;
  }
}
@media (min-width: 1150px) {
  .main-nav__site-list {
    order: 1;
  }
}

@media (min-width: 768px) {
  .main-nav__user-list {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
@media (min-width: 1150px) {
  .main-nav__user-list {
    grid-column: 3/4;
  }
}

.main-nav__logo {
  display: block;
}
@media (min-width: 768px) {
  .main-nav__logo {
    display: flex;
    grid-column: 1/2;
    grid-row: 1/2;
    align-items: center;
    border-bottom: 1px solid #f1f1f1;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    grid-column: 2/3;
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.2;
}

@media (min-width: 1150px) {
  .logo__image {
    width: 150px;
    height: 72px;
  }
}

@media (min-width: 768px) {
  .main-nav__submenu {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
@media (min-width: 1150px) {
  .main-nav__submenu {
    order: 4;
    grid-column: 1/-1;
  }
}

.site-list {
  font-weight: 700;
  font-size: 17px;
  line-height: 141%;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    align-items: center;
    padding: 20px 0;
  }
}
@media (min-width: 1150px) {
  .site-list {
    padding: 0;
    background-color: #ffffff;
    border-bottom: 1px solid #f1f1f1;
  }
}

.site-list__item {
  padding: 29px 65px 27px 76px;
  border-bottom: 1px solid #f1f1f1;
}
@media (min-width: 768px) {
  .site-list__item {
    font-size: 15px;
    padding: 0;
    border: none;
  }
}
@media (min-width: 1150px) {
  .site-list__item {
    display: flex;
    align-items: center;
    height: 88px;
    font-size: 17px;
    line-height: 141%;
    padding-right: 10px;
  }
  .site-list__item:not(:last-child) {
    border-right: 1px solid #f1f1f1;
  }
  .site-list__item:not(:first-child) {
    padding-left: 30px;
  }
}

.site-list__link {
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .site-list__link {
    padding-right: 19px;
  }
}
.site-list__link:hover {
  color: #666666;
}
.site-list__link:active {
  color: #b3b3b3;
}

.user-list {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .user-list {
    display: flex;
    border-bottom: 1px solid #f1f1f1;
  }
}
@media (min-width: 768px) {
  .user-list {
    justify-content: flex-end;
  }
}

.user-list__item {
  border-bottom: 1px solid #f1f1f1;
}
@media (min-width: 768px) {
  .user-list__item {
    display: flex;
    align-items: center;
    padding: 0;
    border: none;
  }
}

.user-list__link {
  display: block;
  text-decoration: none;
  color: #000000;
  padding: 27px 32px 27px 30px;
}
@media (min-width: 768px) {
  .user-list__link--search {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 82px;
    height: 88px;
    border-right: 1px solid #f1f1f1;
    border-left: 1px solid #f1f1f1;
  }
  .user-list__link--search:hover .user-list__icon--search {
    fill: #63d1bb;
  }
  .user-list__link--search:active .user-list__icon--search {
    fill: #d0f1eb;
  }
}
@media (min-width: 768px) {
  .user-list__link--cart {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0;
  }
  .user-list__link--cart:hover .user-list__icon--cart {
    fill: #63d1bb;
  }
  .user-list__link--cart:active .user-list__icon--cart {
    fill: #d0f1eb;
  }
}
.user-list__link:hover {
  color: #666666;
}
.user-list__link:active {
  color: #b3b3b3;
}

.user-list__icon {
  margin-right: 25px;
  vertical-align: middle;
  fill: #cccccc;
}
@media (min-width: 768px) {
  .user-list__icon--search {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .user-list__icon--cart {
    margin-left: 25px;
  }
}

@media (min-width: 768px) {
  .search-text {
    display: none;
  }
}

.submenu {
  display: none;
}
@media (min-width: 768px) {
  .submenu {
    display: flex;
    justify-content: flex-end;
    padding: 20px 0;
  }
}
@media (min-width: 1150px) {
  .submenu {
    justify-content: space-between;
  }
}

.submenu__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .submenu__list {
    display: none;
  }
}
@media (min-width: 1150px) {
  .submenu__list {
    display: flex;
  }
}

.submenu__link {
  font-weight: 700;
  font-size: 15px;
  line-height: 160%;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 1150px) {
  .submenu__link {
    margin-right: 50px;
  }
}
.submenu__link:hover {
  color: #666666;
}
.submenu__link:active {
  color: #b3b3b3;
}

.submenu__description {
  margin: 0;
  padding: 0;
}

.footer {
  display: flex;
  flex-direction: column;
  background-color: #f1f1f1;
  padding: 25px 30px 23px 30px;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 51px 50px 58px 50px;
  }
}

.footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .footer__logo {
    display: block;
    fill: #000000;
  }
}
.footer__logo:hover {
  fill: #63d1bb;
}
.footer__logo:active {
  fill: #bee3dd;
}

.footer__social::after {
  content: "";
  width: 260px;
  border-bottom: 1px solid #ececec;
  padding-top: 28px;
}
@media (min-width: 768px) {
  .footer__social::after {
    display: none;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.social__item {
  margin-right: 27px;
}
.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: inline-flex;
  width: 27px;
  height: 20px;
}

.social__image {
  width: 28px;
  height: 21px;
}
.social__image:hover {
  fill: #46c1ae;
}
.social__image:active {
  fill: #bee3dd;
}

.footer__copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 19px;
  line-height: 95%;
  color: #000000;
  text-decoration: none;
  padding-top: 22px;
  fill: #63d1bb;
}
@media (min-width: 768px) {
  .footer__copyright {
    padding: 0;
  }
}
.footer__copyright:hover {
  fill: #46c1ae;
}
.footer__copyright:active {
  fill: #bee3dd;
}

.copyright__image {
  margin-left: 20px;
}

.intro {
  display: flex;
  flex-direction: column;
}

.intro__title-wrapper {
  min-height: 283px;
  background-color: #f1f1f1;
  background-image: url("../img/intro/hero-mobile.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-resolution: 2dppx) {
  .intro__title-wrapper {
    background-image: url("../img/intro/hero-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .intro__title-wrapper {
    height: 600px;
    background-image: url("../img/intro/hero-tablet.jpg");
    background-position: 0 -50px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .intro__title-wrapper {
    background-image: url("../img/intro/hero-tablet@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .intro__title-wrapper {
    height: 600px;
    background-image: url("../img/intro/hero-desktop.jpg");
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .intro__title-wrapper {
    background-image: url("../img/intro/hero-desktop@2x.jpg");
  }
}

.intro__title {
  align-self: center;
  font-weight: 700;
  font-size: 27px;
  line-height: 111%;
  color: #000000;
  text-align: center;
  margin: 0;
  margin-top: 73px;
}
@media (min-width: 768px) {
  .intro__title {
    font-size: 43px;
    line-height: 116%;
    margin-top: 155px;
  }
}

