.category-products__item {
  display: flex;
  align-items: center;
  background-color: #63d1bb;
  padding-right: 21px;
  cursor: pointer;
}
.category-products__item--interior {
  fill: #3bb19d;
}
.category-products__item--interior:hover {
  background-color: #59d5bc;
  transform: translate(0, -5px);
  fill: #29a592;
}
.category-products__item--interior:active {
  transform: translate(0);
  fill: #44c0aa;
}
.category-products__item--toys {
  fill: #35a999;
}
.category-products__item--toys:hover {
  background-color: #46c1ae;
  transform: translate(0, -5px);
  fill: #239f8e;
}
.category-products__item--toys:active {
  transform: translate(0);
  fill: #37b2a0;
}
@media (min-width: 768px) {
  .category-products__item {
    width: 334px;
    padding-right: 36px;
  }
}
@media (min-width: 1150px) {
  .category-products__item {
    width: 525px;
  }
}

.category-products__link {
  display: inline-block;
  width: 100%;
  padding-left: 24px;
  padding-top: 30px;
  padding-bottom: 31px;
  font-weight: 700;
  font-size: 19px;
  line-height: 126%;
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .category-products__link {
    padding-bottom: 38px;
  }
}
@media (min-width: 1150px) {
  .category-products__link {
    padding-bottom: 62px;
  }
}

.main-product {
  position: relative;
  padding: 30px 30px 42px;
}
@media (min-width: 768px) {
  .main-product {
    padding: 50px 50px 68px;
  }
}
.main-product::after {
  position: absolute;
  content: "";
  height: 7px;
  width: 100%;
